@import url(https://fonts.googleapis.com/css?family=Erica+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
button {
   width: 300px;
   height: 50px;
   background-color: rgba(110,135,214,1);
   color: white;
   border-width: 0px;
   margin-top: 10px;
}
body {
   height: 100%;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}
.container {
   display: flex;
   justify-content: center;
   width: 100%;
}
.box {
   text-align: center;
   width: 85%;
   background-color: white;
   padding: 5%;
   margin-top: 25px;
   border-radius: 10px;
}
.selectBox{
   margin-bottom: 20px;
}
.box > h1, .box > h2, .box > a, .box > p {
   color: rgb(110,135,214);
}
.box > a:hover {
   cursor: pointer;
}
input[type=radio] {
    display:none;
}
.radio {
   min-height: 30px;
   margin: 10px;
   border-style: solid;
   border-color: rgba(110,135,214,1);
   border-width: 2px;
   border-radius: 10px;
   color: rgb(110,135,214);
   cursor: pointer;
}
form {
   margin: 0;
}
label {
   width: 100%;
   height: 30px;
   display: inline-block;
   cursor: pointer;
   padding-top: 10px;
}
input:checked + label{
   background-image: none;
   background-color: rgba(110,135,214,1);
   border-radius: 7px;
   color: white;
}
@media only screen and (min-width: 600px)
{
   .box {
      width: 50%;
   }
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
   background: #C04848;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #5AA7E3, #6f86d6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
.home-mobile, .home {
   text-align: center;
   color: white;
}
.logo-home > h1{
   font-size: 60px;
   font-family: 'Erica One', cursive;
   letter-spacing: 3px;
}
.login-form{
   color: white;
   margin: 2%;
}
.title {
   margin-top: 60px;
}
.form-inputs {
   position: relative;
   top: 0;
}
.form-inputs > button {
   margin-top: 50px;
   width: 100%;
}
.form-inputs > input{
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  background-color: rgba(255,255,255,0);
  color: white;
  border-top: 0;
  border-right: 0;
  border-left: 0;
  border-radius: 0;
}
.form-inputs > input::-webkit-input-placeholder{
   color: white;
   font-size: 17px;
}
.form-inputs > input:-ms-input-placeholder{
   color: white;
   font-size: 17px;
}
.form-inputs > input::-ms-input-placeholder{
   color: white;
   font-size: 17px;
}
.form-inputs > input::placeholder{
   color: white;
   font-size: 17px;
}
.login-form {
   width: 90%;
}
.form-inputs > .title {
   margin-bottom: 125px;
   text-align: center
}
@media screen and (min-width: 770px){
   .home-mobile {
      display: none
   }
   .home {
      display: grid;
      grid-template-columns: 1fr 1fr
   }
   .login{
      grid-column: 2/3
   }
   .login-mobile {
      display: none;
   }
   .logo-img {
      width: 300px;
   }
   .logo-home > h1{
      font-size: 100px;
   }
   .logo-home {
      font-size: 100px;
      text-align: center;
   }
   .home {
      text-align: left;
   }
   .home > button {
      width: 100%;
      height: 50px;
      background-color: #341931;
      color: white;
      border-radius: 50px;
      border-width: 2px;
   }
   .title {
      font-size: 50px;
   }
}

